@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Raleway");
/*Subtitle is too big for mobile*/
@media screen and (max-width: 600px) {
  header h2 {
    visibility: hidden;
    display: none; } }

body {
  background-color: #FFFFFF;
  color: #000000;
  font-family: "Raleway", sans-serif;
  margin: 0px;
  padding: 0px; }

header {
  overflow: hidden;
  width: 100%; }
  header img#logo {
    height: 100px;
    float: left;
    margin: 12px;
    position: relative; }
  header div#header-bar {
    background-color: #e20020;
    min-height: 75px; }
  header h1 {
    color: #FFFFFF;
    margin: 0px;
    padding: 12px 0px 0px 0px; }
  header h2 {
    color: #FFFFFF;
    margin: 0px;
    padding-bottom: 12px; }

div.columns {
  -ms-flex-align: stretch;
  align-items: stretch;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  height: 100%;
  -ms-flex-pack: start;
  justify-content: flex-start;
  max-width: 1000px;
  margin-right: auto;
  margin-left: auto; }
  div.columns div.column {
    padding: 0px 12px 0px 12px; }
  div.columns div.column.left {
    -ms-flex: 5 0 300px;
    flex: 5 0 300px; }
  div.columns div.column.right {
    -ms-flex: 1 4 170px;
    flex: 1 4 170px; }
  div.columns div.segment {
    margin-bottom: 36px;
    overflow: hidden; }
  div.columns div.segment h2 {
    margin: 0px 0px 12px 0px; }

a {
  font-weight: bold;
  color: #e20020;
  text-decoration: none; }

a.see-more-events {
  margin-top: 12px;
  display: block; }

ul.event-list {
  list-style-type: none;
  margin: 0px;
  padding: 0px; }
  ul.event-list li.single-event {
    border: 1px solid #DDDDDD;
    border-radius: 2px;
    margin: 0px 0px 12px 0px;
    padding: 12px; }
    ul.event-list li.single-event h3 {
      color: #FFFFFF;
      font-size: 1.3em;
      text-decoration: none;
      margin: 0px;
      padding: 0px; }
    ul.event-list li.single-event p.date_time::before {
      content: '‚è∞ '; }
    ul.event-list li.single-event p.venue::before {
      content: 'üè¢ '; }
  ul.event-list li.single-event:last-of-type {
    margin: 0px; }

ul.sponsor-list {
  list-style-type: none;
  padding: 0px;
  float: none;
  margin: 0px 0px 12px 0px; }
  ul.sponsor-list li.sponsor-panel {
    margin: 0px 0px 12px 0px; }
    ul.sponsor-list li.sponsor-panel img {
      margin-left: auto;
      margin-right: auto; }

div.twitter-container {
  float: none; }

footer {
  background-color: #e20020;
  color: #FFFFFF;
  min-height: 50px;
  overflow: hidden;
  padding-top: 12px;
  text-align: center;
  width: 100%; }



